.detail-main {
  width: 100%;
  min-height: 100vh;

  padding-top: 30px;
  background: rgba(220, 20, 60, 0.081) fixed;
}
.header {
  display: flex;
  justify-content: center;
  position: relative;
}
.header a {
  position: absolute;
  left: 0px;
}
.detail-main .detail-body {
  width: 100vw;
  display: flex;

  justify-content: space-around;
}
.detail-main h2 {
  font-size: 35px;
  color: rgba(220, 20, 60, 0.493);
}
img {
  height: 40%;
  width: 100%;
}
.detail-body .detail-img {
  display: flex;

  margin-top: 10%;
  width: 35%;
  height: 100%;
  border-radius: 50%;
  overflow: hidden;
}
.detail-body p {
  width: 100%;
  margin: 0;
  font-family: "Chilanka", cursive;
  margin-top: 30px;
  line-height: 20px;
  padding-left: 10px;
  text-indent: 15px;
  color: crimson;
}
.content {
  width: 40%;
}
@media screen and (max-width: 900px) {
  .detail-main {
    text-align: center;
  }
  .detail-main .detail-body {
    width: 100vw;
    display: flex;
    flex-direction: column;
  }
  .detail-body .detail-img {
    text-align: center;
    margin: 20px auto;
  }
  .content {
    width: 80%;
    margin: 10px auto;
  }
}
