* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.container {
  background-color: rgba(220, 20, 60, 0.081);
  max-width: 100%;
  min-height: 100vh;
  margin: 0px auto;
  padding-top: 30px;
}
h1 {
  text-align: center;
  font-size: 50px;
  font-family: "Chilanka", cursive;
  color: crimson;
}
h3 {
  font-family: "Chilanka", cursive;
  font-size: 25px;
}
.recipes {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}
.card {
  position: relative;
  text-align: center;
  overflow: hidden;
  margin: 30px;
  border-radius: 50px;
  padding-bottom: 10px;
  width: 220px;
  height: 370px;
  background-color: rgba(220, 20, 60, 0.359);
  color: rgba(220, 20, 60, 0.835);
}
.card img {
  width: 100%;
  height: 70%;
}
.card img:hover {
  width: 100%;
  padding-bottom: 7px;
  transition: all 1s;
}
.card .card-body {
  height: 30%;
  display: flex;
  flex-direction: column;
}
.category {
  color: aliceblue;
}
a {
  margin: 0 auto;
  width: fit-content;
  color: crimson;
  border: 1px solid crimson;
  padding: 0 5px;
  border-radius: 50px;
  margin-top: 10px;
  background-color: aliceblue;
  text-decoration: none;
}

/* search section */
.form-control {
  padding: 10px 15px;
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 15px;
  overflow: hidden;
  width: 70%;
}

form {
  width: 300px;
  margin: 30px auto;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  border-radius: 50px;
  position: relative;
  overflow: hidden;
}
.btn {
  width: 30%;
  border: none;
  outline: none;
  padding: 10px 15px;
  font-size: 18px;
  cursor: pointer;
  position: absolute;
  right: 0;
  color: crimson;
  background-color: rgba(220, 20, 60, 0.359);
}
